<template>
<div class='orderhome'>
<p class='pp'>近期订单</p>
    <Modal
        v-model="modal1"
        title="评价"
        @on-ok="ok"
        @on-cancel="cancel">
         <Input v-model="commentContent" type="textarea" :rows="4" placeholder="Enter something..." />
          <Rate allow-half v-model="valueHalf" />
    </Modal>
<template v-for="orderVo in orderVoList">
        <Card :key="orderVo.orderId" class="ordercard">
                <p>创建时间:{{orderVo.orderTime}}</p>              
                <p>支付金额:{{orderVo.allPrice}}</p>
                <p v-if="orderVo.orderState==0">未接单</p>
                <p v-if="orderVo.orderState==1">已接单</p>
                <p v-if="orderVo.orderState==2">已完成</p>
                <p v-if="orderVo.orderState==3">已评价</p>
                 <Button type="warning" v-if="orderVo.orderState==2" @click="commentPage(orderVo)">评价</Button>              
                <br>
                <template v-for="foodVo in orderVo.foodAndNum">                   
                <a :key="foodVo.foodId" @click="goCanteen(foodVo.canteenId)">
                 {{foodVo.foodName}}   
                </a>
                <b :key="foodVo.foodId">
                  {{foodVo.foodNum}}         
                </b>    
                </template>
            </Card>
</template>
</div>    
</template>
<script>
import {selectUserOrder,addOneComment} from '@/api/api.js'
export default {
    data(){
        return{
            user:'',
            orderVoList:[],
            modal1: false,
            commentContent:'',
            commentVo:'',
            valueHalf: 2.5
        }
    },
    methods:{
        goCanteen(canteenId){
           this.$router.push({name:'clientFoodList',query:{canteenId:canteenId}})
        },
        commentPage(orderVo){
            this.modal1=true
            this.commentVo=orderVo
        },
         ok () {
             if(!this.commentContent){
                 this.$Message.error('请输入评论内容')
                 return
             }
             let list=this.commentVo.foodAndNum
             let canteenId=list[0].canteenId
             let data={commentContent:this.commentContent,userId:this.user.userId,orderId:this.commentVo.orderId,canteenId:canteenId}
               addOneComment(data).then(res=>{
                   this.$Message.success('评价成功')
                   this.$router.push({name:'canteenList'})
               })
            },
            cancel () {
            }
    },
    mounted(){
         this.user=JSON.parse(localStorage.getItem('user'))
         let data={userId:this.user.userId}
         selectUserOrder(data).then(res=>{
             this.orderVoList=res.data.data
         })
    }
}
</script>
<style lang="less">
.orderhome{
    .pp{
        font-size:20px;
        
    }
    .ordercard{
            margin:20px 0px 20px 0px;
        }

}
</style>

